* {
  box-sizing: border-box;
  margin:0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}
.container {
  padding: 0 1rem;
}

.btn {
  display: inline-block;
  border: none;
  color: #fff;
  padding: 7px 10px;
  cursor: pointer;
}
.btn:hover {
  background: #666;
}
.delBtn {
  background: none;
  color: #333;
  border: none;
  padding:5px 9px;
  cursor: pointer;
  float: right;
  outline: none;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.delBtn:hover {
  color: red;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
